<template>
  <div class="hello">
    <textarea v-model="input">
    </textarea>
    <textarea :value="output"></textarea>
  </div>
</template>

<script>
/* eslint-disable */
import yamljs from 'yamljs'
export default {
  name: 'HelloWorld',
  data () {
    return {
      input : `App\Entity\Portafoglio\ListinoVoce:
    type: entity
    table: ListinoVoce  
    fields:
        id:
            type: integer
            id: true
            length: 2
        nome:
            type: string
            length: 25
            notnull: true
        codice:
            type: string
            length: 100
            notnull: true   
        descrizione:
            type: string
            length: 100
            notnull: true
        numeroRate:
            column: num_rate
            type: integer
            notnull: true`,
      msg: 'Welcome to Your Vue.js App',
    }
  },
  computed : {
    output : function(){
      let object = yamljs.parse(this.input);
      //console.log(object);
      try{
        let title = Object.keys(object)[0];
        console.log("TITLE",title)
        let entity = title.split(/(?=[A-Z])/).filter((v,i)=>{ return i > 2}).join("");
        let namespace = title.split(/(?=[A-Z])/).filter((v,i)=>{return  i < 3}).join("\\");
        
        let text = `<?php
namespace ${namespace};
use JMS\\Serializer\\Annotation\\ExclusionPolicy;
use JMS\\Serializer\\Annotation\\Expose;

/**
 * @ExclusionPolicy("all")
 */

class ${entity} { \n`;
        Object.keys(object[title].fields).forEach((v)=>{
          text+= `\n /**
  * @var ${object[title].fields[v].type}
  */\n`
          text+= `  private $${v};\n\n`;
        })
        Object.keys(object[title].fields).forEach((v)=>{
          text+= ` /**
  * Get ${v}
  *
  * @return string
  */
  public function get${this.jsUcfirst(v)}() {
        return $this->${v};
  }
  
 /**
  * Set ${v}
  *
  * @param string $${v}     	
  * @return ${entity}
  */
  public function set${this.jsUcfirst(v)}($${v}) {
          $this->${v} = $${v};
          return $this;
  }\n
`;
        })
      // setting the intro;
      return text;
      }catch(e){
        return "Error parsing orm.yaml"+e
      }
     
    }
  },
  methods : {
    jsUcfirst(string) 
      {
          return string.charAt(0).toUpperCase() + string.slice(1);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  height : 100vh;
  width : 100vw;
  display : flex;
  flex-flow : row;
}
textarea {
  width : 100%;
  height : 100%;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



